import nav from '@/components/nav/nav.vue';
import {mapState} from 'vuex';
import wx from 'weixin-jsapi';
export default{
    data() {
        return {
            storeCode: '',
            wxConfig: {}
        };
    },
    computed: {
        ...mapState(['global', 'weixin'])
    },
    watch: {
    },
    methods: {
        settlement() {
            var that = this;
            this.axios({
                method: 'post',
                url: '/Api/GuiBankPay',
                data: '{"id":19,"jsonrpc":"2.0","method":"getWeChatJsSdkData","params":{"url":"' + window.location.href + '"}}',
                contentType: 'application/json',
                dataType: 'json'
            }).then((result) => {
                wx.config({
                    debug: false,
                    appId: result.data.result.appId,
                    timestamp: result.data.result.timestamp,
                    nonceStr: result.data.result.nonceStr,
                    signature: result.data.result.signature,
                    // 所有要调用的 API 都要加到这个列表中
                    // 要调用的微信接口
                    jsApiList: [
                        'translateVoice',
                        'scanQRCode'
                    ]
                });
                wx.ready(function() {
                    wx.checkJsApi({ // 判断当前客户端版本是否支持指定JS接口
                        jsApiList: [
                            'translateVoice',
                            'scanQRCode'
                        ],
                        success: function (res) { // 以键值对的形式返回，可用true，不可用false。如：{"checkResult":{"scanQRCode":true},"errMsg":"checkJsApi:ok"}
                            if (res.checkResult.scanQRCode === true) {
                                console.log('success');
                                wx.scanQRCode({
                                    needResult: 1, // 默认为0，扫描结果由微信处理，1则直接返回扫描结果，
                                    scanType: ['qrCode', 'barCode'], // 可以指定扫二维码还是一维码，默认二者都有
                                    success: function(res) {
                                        alert(res.resultStr);
                                        that.$router.push({path: `/settlement/${res.resultStr}`});
                                        console.log(res.resultStr);// 当needResult 为 1 时，res.resultStr为扫码返回的结果
                                    }
                                });
                            } else {
                                alert('抱歉，当前客户端版本不支持扫一扫');
                            }
                        },
                        fail: function (res) { // 检测getNetworkType该功能失败时处理
                            alert('fail' + res);
                        }
                    });
                });
            });
            // this.$router.push({path: `/settlement/${this.storeCode}`});
        },
        storeInfoPage() {
            alert(123);
        }

    },
    created () {
        console.log(wx);
    },
    components: {
        'v-nav': nav
    }
};



